import { KwsTask, UploadEvent, KwsTaskResults } from '../models';
import { Observable } from 'rxjs';
import { KwsClientUtilsService } from './kws-client-utils.service';
import { Router } from '@angular/router';
import { LocalStorageService } from '../local-storage/local-storage.service';
import { MOCK_KWS_TASKS } from '../../data';
import { getFakeKeywords, getFakeAudioFilenames, getFakeAudioDurations } from 'src/app/shared/utils';
import { generateMockKwsTaskResults, MockAudioFile, coerceKwsTaskName } from '../utils';
import { TimeService } from '../time/time.service';
import { KwsAudioDataService } from '../audio';
import { KwsApiServiceBase } from '../kws-api/kws-api.service.base';
