@Injectable({
    providedIn: 'root'
})
export class KwsClientService {

    // TODO: probably remove
    private _latestTaskId: string | undefined;

    get latestTaskId() {
        return this._latestTaskId;
    }


    constructor(
        private apiService: KwsApiServiceBase,
        // private kwsLocalStorage: KwsClientLocalStorageService,
        private audioData: KwsAudioDataService,
        private tasksStore: KwsTasksStoreService,
        private config: KwsClientConfigService,
        private utils: KwsClientUtilsService,
        private logger: LoggerService,
        private router: Router,
        private time: TimeService
    ) {
        (window as any).kwsClient = this;
        (window as any).audioData = audioData;
    }

    // TODO
    init(state: any) {
        this.log('initializing');

        // this.localStorageService.getItem('')

        const averageDuration = 360; // 3600; // 1h in seconds
        // this.tasksStore.init()

        for (const task of MOCK_KWS_TASKS) {
            const keywords = getFakeKeywords(100);
            const filenames = getFakeAudioFilenames(10);
            const durations = getFakeAudioDurations(10, averageDuration, 0.5);
            const audioFiles: MockAudioFile[] = filenames.map((name, i) => ({
                name,
                duration: durations[i]
            }));


            task.results = generateMockKwsTaskResults(keywords, audioFiles);
            this.tasksStore.addTask(task);
        }
    }

    submitTask(task: KwsTask): Observable<UploadEvent<any>> {
        this.log('submitting new task');
        const submissionTime = this.time.currentTime;
        task = { ...task, submissionTime };
        task.name = coerceKwsTaskName(task.name, this.time.getTimestamp());
        const taskId = this.tasksStore.addTask(task);
        this._latestTaskId = taskId;
        this.audioData.putTaskFiles(taskId, task.audioFiles as File[]);
        this._awaitTaskCompletion(taskId);
        return this.apiService.postTask(task);
    }

    fetchTaskResults(taskId: number) {

    }

    fetchAllTasks() {

    }

    private _awaitTaskCompletion(taskId: string) {
        // TODO: read
        //  https://www.quora.com/What-does-setTimeout-with-a-0ms-delay-do-Is-this-some-trick-to-spawn-a-new-thread-but-why


        this.log('starting wait on task completion');
        /*
            TODO: implem waiting queue (if many task pending at once)

        */
        setTimeout(() => {
            this.apiService.getTaskResults(taskId).subscribe(results => {
                this.log('receiving results from KwsApiService');
                this.tasksStore.assignTaskResults(taskId, results);
                this._latestTaskId = taskId;
                this.router.navigate([`/${taskId}/results`]);
            });
        }, 0);
    }

    // beforeUnload() {
    //     const stateJson = JSON.stringify(this.tasksStore.tasks);
    //     this.storageService.setItem('', stateJson);
    // }


    getState() {
        return this.tasksStore.tasks;
    }

    // TODO: remove these 2
    private loadState() {
        // = this.localStorageService.getKwsTasks();
        // this.tasksStoreService.init()
    }

    private saveState() {
        // this.localStorageService.
    }


    private log(message: string) {
        this.logger.log(`KwsClientService: ${message}`);
    }
}
