import { Injectable } from '@angular/core';
import { KwsApiServiceBase } from './kws-api.service.base';
import { KwsTask, UploadEvent, KwsTaskResults, UploadEventType } from '../models';
import { Observable, throwError, of } from 'rxjs';
import { HttpErrorResponse, HttpEvent, HttpEventType, HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map } from 'rxjs/operators';
import { parseSpotFile } from '../utils';
import { KwsClientConfigService } from '../kws-client/kws-client-config.service';
import { KwsClientUtilsService } from '../kws-client/kws-client-utils.service';
import { LoggerService } from '../logger/logger.service';



@Injectable({
    providedIn: 'root'
})
export class KwsApiService extends KwsApiServiceBase {

    constructor(
        private config: KwsClientConfigService,
        private utils: KwsClientUtilsService,
        private http: HttpClient,
        protected logger: LoggerService
    ) {
        super(logger);

    }

    // TODO: fix errors
    postTask(task: KwsTask): Observable<UploadEvent<any>> {
        if (!task.audioFiles || !task.vocabFile) {
            const message = 'postTask failed: task does not have vocabFile or audioFiles';
            this.logError(message);
            return of({ type: UploadEventType.Failure, message } as any);
        }

        // const custom = task.dataset === 'custom';
        // const configFile = createCfgxFile(settings, custom);
        const vocabKey = this.utils.resolveVocabKey(task.vocabFile.name);

        const fd = new FormData();
        fd.append('language', task.language);
        // fd.append('dataset', task.dataset);

        // fd.append('email', task.email);
        // fd.append('configfile', configFile);
        fd.append(vocabKey, task.vocabFile, task.vocabFile.name);
        if (task.useStoredDataset) {
            task.audioFiles.forEach(file => fd.append('audiofile', file));
        }

        (window as any).formData = fd;
        // const postTaskEvents = window['postTaskEvents'] = []; // DEBUG

        const headers = new HttpHeaders();
        // headers.append('Authorization', 'Basic ' + btoa('tester:wordspottingdemo2019'));

        return this.http.post(this.config.newTaskEndpoint, fd, {
            reportProgress: true,
            observe: 'events',
            responseType: 'text',
            headers
        }).pipe(
            map(this.mapToUploadEvent),
            catchError(this.handleError('postTask')),
        );
    }


    getTaskResults(taskId: string): Observable<KwsTaskResults> {
        return this.http.get(this.config.taskResultsEndpoint, {
            responseType: 'text',
        }).pipe(
            map((data: string) => ({
                candidates: parseSpotFile(data)
            })),
            catchError(this.handleError('getTastResults'))
        );
    }


    private mapToUploadEvent(event: HttpEvent<any>): UploadEvent<any> {
        // if (event.type !== HttpEventType.UploadProgress) postTaskEvents.push(event); // DEBUG
        switch (event.type) {
            case HttpEventType.UploadProgress:
                const percent = event.total ? event.loaded / event.total : 0;
                return {
                    type: UploadEventType.Progress,
                    percentDone: percent
                };
            case HttpEventType.Response:
                return {
                    type: UploadEventType.Response,
                    body: event.body,
                };
            default:
                return {
                    type: UploadEventType.Unknown
                };
        }
    }


    /**
     * Handle Http operation that failed.
     * @param operation - name of the operation that failed
     */
    protected handleError(operation = 'operation') {
        return (err: HttpErrorResponse): Observable<never> => {
            // in a real world app, we may send the server to some remote logging infrastructure
            // instead of just logging it to the console
            let errorMessage: string;
            if (err.error instanceof Error) {
                // A client-side or network error occurred. Handle it accordingly.
                errorMessage = `An error occurred: ${err.error.message}`;
            } else {
                // The backend returned an unsuccessful response code.
                // The response body may contain clues as to what went wrong,
                errorMessage = `Backend returned code ${err.status}, body was: ${err.error}`;
            }

            this.logError(`${operation} failed: ${errorMessage}`);

            return throwError(errorMessage);
        };
    }

}
