import { Component, ViewChild, Input, OnChanges, SimpleChanges, AfterViewInit, ViewEncapsulation, ChangeDetectionStrategy } from '@angular/core';
import { columnFactory, createDS, PblNgridComponent } from '@pebula/ngrid';
import { KwsTaskResults } from 'src/app/core/models';
import { KwsAudioDataService } from 'src/app/core/audio';
import { PblNgridRowEvent, PblNgridCellEvent } from '@pebula/ngrid/target-events';
import { time100usToString } from 'src/app/shared/utils';
import { KwsTasksStoreService } from 'src/app/core/kws-tasks-store/kws-tasks-store.service';
import { LoggerService } from 'src/app/core/logger/logger.service';


const TIME_MS_TO_STRING = (time: number) => {
    return time100usToString(time * 10);
};


const COUNTRY_GETTER = {
    currency: (row: any) => COUNTRY_GETTER.data.countries[row.country].currencies[0],
    name: (row: any) => COUNTRY_GETTER.flag(row) + ' ' + COUNTRY_GETTER.data.countries[row.country].name,
    flag: (row: any) => COUNTRY_GETTER.data.countries[row.country].emoji,
    data: undefined as any
};

const progressBarStyle = (value: number) => {
    if (value > 90) {
        return { color: 'white', background: 'darkgray' };
    }
    if (value >= 40 && value <= 90) {
        return { color: 'white', background: 'lightgray' };
    }
    return { color: 'black', background: 'red' };
};

const COLUMNS = columnFactory()
    .default({ width: '150px', reorder: true, resize: true })
    .table(
        // tslint:disable-next-line: max-line-length
        // { prop: 'drag_and_drop_handle', type: 'drag_and_drop_handle', minWidth: 24, width: '', maxWidth: 24, wontBudge: true, resize: false, },
        { prop: 'id', sort: true, width: '50px', wontBudge: true, pIndex: true },
        { prop: 'filename', width: '300px', sort: true },
        { prop: 'keyword', sort: true },
        { prop: 'filler', sort: true },
        // { prop: 'score', headerType: 'country', type: { name: 'countryNameDynamic', data: COUNTRY_GETTER }, width: '150px' },
        { prop: 'timeFrom', sort: true, transform: TIME_MS_TO_STRING },
        { prop: 'timeTo', sort: true, transform: TIME_MS_TO_STRING },
        // { prop: 'rating', type: 'starRatings', width: '120px' },
        { prop: 'score', sort: true, type: { name: 'progressBar', data: { style: progressBarStyle } }, width: '100px' },
        { prop: 'selection', minWidth: 48, width: '', maxWidth: 48, wontBudge: true, resize: false, },
    )
    .header(
        { rowClassName: 'pbl-groupby-row' },
        { id: 'pbl-groupby-row', type: 'pbl-groupby-row', label: ' ' },
    );


@Component({
    selector: 'app-kws-table',
    templateUrl: './kws-table.component.html',
    styleUrls: ['./kws-table.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class KwsTableComponent implements OnChanges, AfterViewInit {


    get runChangeDetection() {
        this.log('running change detection');
        return true;
    }


    // @Input() taskResults: KwsTaskResults | undefined;
    @Input() taskId: string | undefined;


    dataSource = createDS<KeywordCandidate>()
        .onTrigger((event) => {
            if (this.taskId) {
                const results = this.kwsTasksStore.getTaskResults(this.taskId);
                if (results) {
                    return results.candidates;
                }
            }
            return [];
        })
        .create();

    columns = COLUMNS.build();

    @ViewChild(PblNgridComponent, { static: true }) table: PblNgridComponent<KeywordCandidate> | undefined;

    /**
     *
     *
     */
    constructor(
        private kwsAudioData: KwsAudioDataService,
        private kwsTasksStore: KwsTasksStoreService,
        private logger: LoggerService,
    ) {
        // ds.getCountries().then( c => COUNTRY_GETTER.data = c );
    }

    ngAfterViewInit() {

    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.taskId) {
            this.refresh();
        }
    }


    refresh(): void {
        this.dataSource.refresh();
    }

    dropped(e: any) {
        console.log(e);
    }



    onClickEvents(event: PblNgridRowEvent<KeywordCandidate> | PblNgridCellEvent<KeywordCandidate>) {
        let cellSuffix = '';
        if (isCellEvent(event)) {
            cellSuffix = `  CELL: ${event.colIndex}`;
        } else {
            if (event.root) {
                cellSuffix = ` [Bubbled from CELL: ${event.root.colIndex}]`;
            }
        }
        const targetItem = this.dataSource.renderedData[event.rowIndex];

        // TODO: use filename as key or compute index range when reading and assing it a key in hashmap
        this.kwsAudioData.playSlice(targetItem.filename, targetItem.timeFrom, targetItem.timeTo);

        this.log(`CLICK EVENT at ROW: ${event.rowIndex}${cellSuffix}\nType: ${event.type}\nSubType: ${event.subType}`);
    }

    private log(message: string) {
        this.logger.log(`KwsTableComponent: ${message}`);
    }
}

function isCellEvent<T>(event: PblNgridRowEvent<T> | PblNgridCellEvent<T>): event is PblNgridCellEvent<T> {
    return !!(event as PblNgridCellEvent<T>).cellTarget;
}
