import { Routes } from '@angular/router';
import { TasklistComponent } from './tasklist.component';
import { TasklistListComponent } from './tasklist-list/tasklist-list.component';
import { TaskResultsComponent } from '../task-results/task-results.component';

export const TASKLIST_ROUTES: Routes = [
    {
        path: 'tasklist',
        component: TasklistComponent,
        children: [
            { path: '', component: TasklistListComponent },
        ]
    },
    {
        path: 'task/:id',
        redirectTo: 'task-results/:id',
        pathMatch: 'full'
    }
];
