
import { parseTimeMs } from 'src/app/shared/utils';


export function parseSpotFile(str: string): KeywordCandidate[] {
    const TIME_COEF = 10;
    const lines: any = str.match(/[^\r\n]+/g);
    const header: string = lines.shift();

    const candidates: KeywordCandidate[] = new Array(lines.length);

    for (let i = 0, I = lines.length; i < I; i++) {

        const lineParts = lines[i].split(/\s+/);
        if (lineParts.length !== 9) {
            throw new Error(`Invalid line ${i + 2}`);
        }

        candidates[i] = {
            // index: i,
            id: i,
            filename: lineParts[0],
            keyword: lineParts[1],
            score: parseFloat(lineParts[2]),
            timeFrom: TIME_COEF * parseTimeMs(lineParts[3]),
            timeTo: TIME_COEF * parseTimeMs(lineParts[4]),
            filler: ''
        };

        // spots[i] = parseInt(lineParts[5], 10);
        // spots[i] = parseInt(lineParts[6], 10);
        // spots[i] = parseFloat(lineParts[7]);
        candidates[i].filler = lineParts[8];
    }

    return candidates;
}
