import { catchError, tap, finalize, switchMap, mergeAll, endWith, takeLast, last, filter, map } from 'rxjs/operators';
import { Observable, PartialObserver, Subject, defer, combineLatest, Subscription, of, merge } from 'rxjs';
import { MatDialog } from '@angular/material/dialog';
import { KwsTask, UploadResponse, UploadProgressEvent, UploadEventType } from '../core/models';
import { KwsClientService } from '../core/kws-client/kws-client.service';
import { KwsClientConfigService } from '../core/kws-client/kws-client-config.service';
import { LoggerService } from '../core/logger/logger.service';
// import { requiredFileTypeValidator } from '../validators/file-validators';

