@Component({
    selector: 'app-task-submission',
    templateUrl: './task-submission.component.html',
    styleUrls: ['./task-submission.component.scss'],
    changeDetection: ChangeDetectionStrategy.Default
})
export class TaskSubmissionComponent implements OnInit {

    get runChangeDetection() {
        this.log('running change detection');
        return true;
    }

    panelOpenState = false;

    uploading = false;
    uploadSuccessful = false;
    formSubmitAttemt = false;
    progress = 0;
    uploadSubscription = Subscription.EMPTY;

    form: FormGroup = this.formBuilder.group({
        name: [null],
        vocabFile: [null, Validators.required],
        audioFiles: [null, Validators.required],
        language: [null, Validators.required],

        advanced: this.formBuilder.group({
            //     phonemeType: [null, Validators.required],
            numHypotheses:  [0, Validators.required],
            pruneThreshold: [0, Validators.required],
            spotThreshold:  [0, Validators.required],
            dispThreshold:  [0, Validators.required],
            //     scaleFactor: [null, Validators.required],
            //     frameShiftInSamples: [null, Validators.required],
            //     frameOffsetDueParametrization: [null, Validators.required],
        })
    });

    get languages() {
        return this.config.languages;
    }

    get selectedVocabFile() {
        const fileList = this.form.controls.vocabFile.value as FileList;
        return (fileList && fileList.length !== 0) ? (fileList.item(0) as File).name : null;
    }

    get selectedAudioFiles() {
        const fileList = this.form.controls.audioFiles.value as FileList;
        // console.log('running change detection', Date.now())
        // console.log(fileList)
        return (fileList && fileList.length !== 0) ? Array.from(fileList) : null;
    }

    get disabled() {
        return this.uploading || this.uploadSuccessful;
    }

    constructor(
        private kwsClient: KwsClientService,
        private config: KwsClientConfigService,
        private formBuilder: FormBuilder,
        private cd: ChangeDetectorRef,
        private dialog: MatDialog,
        private logger: LoggerService,
    ) {

        (window as any).taskEdit = this;

    }

    _fileChangesSub: Subscription = Subscription.EMPTY;

    ngOnInit(): void {
        // this.setDefaultFormValues();

        this.log('Init');

        this._fileChangesSub = merge(
            this.form.controls.vocabFile.valueChanges,
            this.form.controls.audioFiles.valueChanges
        ).subscribe(() => {
            this.log('File Changes');
            this.cd.detectChanges();
        });
    }

    setDefaultFormValues() {
        this.form.patchValue({
            // language: this.languages[0],

            advanced: {
                numHypotheses:  0,
                pruneThreshold: 0,
                spotThreshold:  0,
                dispThreshold:  0,
            }
        });
    }


    onSubmit(task: any) {
        (window as any).task = task;
        task.vocabFile.item(0);

        if (this.uploading) {
            this.uploadSubscription.unsubscribe();
        }


        if (this.form.valid) {
            this.uploading = true;
            this.form.disable();

            this.uploadSubscription = this.kwsClient.submitTask(this.processKwsTask(task)).pipe(
                tap(event => {
                    switch (event.type) {
                        case UploadEventType.Progress:
                            this.progress = 100 * event.percentDone;
                            this.cd.detectChanges();
                            break;
                        case UploadEventType.Response:
                            // event.body
                            // this.openSuccessDialog(); TODO: uncomment
                            break;
                        default:
                            break;
                    }
                }),
                catchError((err: Error) => {
                    // this.openFailureDialog(); TODO: uncomment
                    return of(undefined);
                }),
                finalize(() => {
                    // this.uploadProgress = 0;
                    this.uploading = false;
                })
            ).subscribe();
        }

        this.form.markAllAsTouched();
        this.cd.detectChanges();
    }

    reset() {
        this.form.enable();
        this.form.reset();
        this.formSubmitAttemt = false;

        this.setDefaultFormValues();
    }

    processKwsTask(task: any): KwsTask {
        if (!task.name) {
            task.name = '';
        }

        const taskCopy = {
            ...task,
            vocabFile: task.vocabFile.item(0),
            audioFiles: Array.from(task.audioFiles)
        };
        return taskCopy;
    }

    log(message: string) {
        this.logger.log(`TaskEditComponent: ${message}`);
    }

    // TODO: add dialogs and uncomment

    // openSuccessDialog(): void {
    //     const dialogRef = this.dialog.open(TaskUploadSuccessDialogComponent, {
    //         width: '250px',
    //         // data: { name: this.name, animal: this.animal }
    //     });

    //     dialogRef.afterClosed().subscribe(result => {
    //         console.log('The success dialog was closed');
    //         // this.animal = result;
    //     });
    // }

    // openFailureDialog() {
    //     const dialogRef = this.dialog.open(TaskUploadFailureDialogComponent, {
    //         width: '250px',
    //     });

    //     dialogRef.afterClosed().subscribe(result => {
    //         console.log('The failure dialog was closed');
    //         // this.animal = result;
    //     });
    // }

}
