import { KwsTask } from 'src/app/core/models';
import { Title } from '@angular/platform-browser';


export class KwsTasklistItem implements TasklistItem {

    get id() {
        return this.task.id;
    }

    get title() {
        return this.task.name || '';
    }

    get kwcCount(): number {
        if (this.task.results) {
            return this.task.results.candidates.length;
        }
        return NaN;
    }

    get isCompleted() {
        return this.task.isCompleted;
    }

    checked = false;

    constructor(private task: KwsTask) {

    }
}


export interface TasklistItem {
    readonly id: string;
    readonly title: string;
    readonly kwcCount: number;
    readonly isCompleted: boolean;
    checked: boolean;
}


export type TasklistFilter = 'ALL' | 'COMPLETED' | 'PENDING';
