import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class AudioContextService {

    private _ctx: AudioContext | undefined;

    get ctx(): AudioContext {
        if (!this._ctx) {
            this._ctx = new AudioContext(); // instance after a user gesture
        }

        return this._ctx;
    }

    constructor() { }
}
