import { UploadEvent, KwsTaskResults, KwsTask } from '../models';
import { Observable } from 'rxjs';
import { LoggerService } from '../logger/logger.service';



export abstract class KwsApiServiceBase {

    constructor(protected logger: LoggerService) { }

    abstract postTask(task: KwsTask): Observable<UploadEvent<any>>;


    abstract getTaskResults(taskId: string): Observable<KwsTaskResults>;


    protected log(message: string) {
        // console.log(`KwsTaskService: ${message}`);
        this.logger.log(`KwsTaskService: ${message}`);
    }

    protected logError(message: string) {
        this.logger.logError(`KwsTaskService: ${message}`);
    }
}
