import { Injectable } from '@angular/core';
import { KwsClientConfigService } from './kws-client-config.service';

@Injectable({
    providedIn: 'root'
})
export class KwsClientUtilsService {

    constructor(
        private config: KwsClientConfigService
    ) { }


    /**
     *  Returns appropriate vocab key for a given vocab filename
     */
    resolveVocabKey(filename: string): string {
        // find out vocab type

        const fileExt = filename.split('.').pop();
        const vocab = this.config.acceptedVocabs.find((v) => v.fileExt === fileExt);

        if (vocab) {
            return vocab.name;
        }

        throw new Error('Invalid vocab file extension');
    }
}
