import { Injectable, NgZone } from '@angular/core';
import { AudioPlayerService } from './audio-player.service';
import { AudioContextService } from './audio-context.service';
import { BehaviorSubject, Observable } from 'rxjs';
import { readAudioFile, audioBufferSlice, generateWhiteNoise } from './utils';
import { KwsTasksStoreService } from '../kws-tasks-store/kws-tasks-store.service';
import { KwsApiService } from '../kws-api/kws-api.service';
import { LoggerService } from '../logger/logger.service';

/**
 *
 * TODO: optravit dokumentaci
 * X|  Nat audio data podle poteby
 *
 * X| Kdy jsou dostupn v "tasks-store", tak je nate odtud, jinak je zsk od serveru
 *
 */
@Injectable({
    providedIn: 'root'
})
export class KwsAudioDataService {

    taskFilesCache = new Map<string, File[]>();

    loadedTaskId: string | undefined;

    sampleRate = 16000;

    data = new Map<string, AudioBuffer>();


    private _readySubject = new BehaviorSubject<boolean>(false);
    ready$: Observable<boolean> = this._readySubject.asObservable();


    dataAvailable = false;

    get ctx() {
        return this.audioContextService.ctx;
    }

    get first() {
        return this.data.values().next().value;
    }

    get entries(): IterableIterator<[string, AudioBuffer]> {
        return this.data.entries();
    }

    get durations() {
        return [...this.data.values()].map(audioBuffer => audioBuffer.duration);
    }

    constructor(
        private audioContextService: AudioContextService,
        private tasksStoreService: KwsTasksStoreService,
        private kwsApiService: KwsApiService,
        private audioPlayer: AudioPlayerService,
        private ngZone: NgZone,
        private logger: LoggerService
    ) {
        // this.ctx = new AudioContext(); // instance after a user gesture
        // this.onended = () => {};
        (window as any).readAudioFile = readAudioFile;
        (window as any).audioBufferSlice = audioBufferSlice;

        this.tasksStoreService.taskAdded$.subscribe(task => {
            if (task.audioFiles) {
                this.readAudioFiles(task.audioFiles);
            }
        });

    }

    putTaskFiles(taskId: string, files: File[]) {
        this.taskFilesCache.set(taskId, files);
    }

    async loadTaskData(taskId: string) {

        // check if task data is already loaded
        if (this.loadedTaskId === taskId) {
            return;
        }

        this.loadedTaskId = undefined;

        // check if task data files are stored in cache
        const taskFiles = this.taskFilesCache.get(taskId);
        if (taskFiles) {
            await this.readAudioFiles(taskFiles);
        }

        // else try fetch audio data from server
        // this.kwsApiService.


        this.loadedTaskId = taskId;
    }


    playSlice(key: string, startTime: number, endTime: number) {
        const startSamples = Math.round(startTime * this.sampleRate * 0.0001);
        const endSamples = Math.round(endTime * this.sampleRate * 0.0001);

        let buffer: AudioBuffer | undefined;
        if (!this.dataAvailable) {
            buffer = generateWhiteNoise(this.ctx, endSamples - startSamples);
        } else {
            buffer = this.data.get(key);
            if (buffer) {
                buffer = audioBufferSlice(this.ctx, buffer, startSamples, endSamples);
            } else {
                buffer = generateWhiteNoise(this.ctx, endSamples - startSamples);
            }
        }

        this.audioPlayer.playBuffer(buffer);
    }


    async readAudioFiles(audioFiles: File[]) {
        this._readySubject.next(false);

        this.data.clear();

        // this.ngZone.runOutsideAngular(async () => {
        for (const file of audioFiles) {
            try {
                const audioBuffer = await readAudioFile(this.ctx, file);
                this.data.set(file.name, audioBuffer);
            } catch (error) {
                this.logError(`audio file read error: ${file.name}, ${error.message}`);
            }
        }
        // })

        setTimeout(() => {
            this.dataAvailable = true;
            this._readySubject.next(true);
        }, 2000);

    }

    logError(message: string) {
        this.logger.logError(message);
    }
}
