import { Component, OnInit } from '@angular/core';
import { Observable, BehaviorSubject } from 'rxjs';
import { TasklistFilter, TasklistItem, KwsTasklistItem } from '../models/tasklist.model';
import { map } from 'rxjs/operators';
import { KwsTask } from 'src/app/core/models';
import { KwsTasksStoreService } from 'src/app/core/kws-tasks-store/kws-tasks-store.service';
import { LoggerService } from 'src/app/core/logger/logger.service';


@Component({
    selector: 'app-tasklist-list',
    templateUrl: './tasklist-list.component.html',
    styleUrls: ['./tasklist-list.component.scss']
})
export class TasklistListComponent {

    _filterSubject = new BehaviorSubject<TasklistFilter>('ALL');

    // selectedItems: KwsTasklistItem[] = [];

    items$: Observable<TasklistItem[]>;
    filter$ = this._filterSubject.asObservable();
    // showActionButtons$: Observable<boolean>;

    constructor(
        private tasksStore: KwsTasksStoreService,
        private logger: LoggerService
    ) {
        this.log('running constructor');
        this.items$ = this.tasksStore.tasks$.pipe(map(tasks => tasks.map(this.mapToTasklistItem)));

        (window as any).taskList = this;
    }

    setFilter(filter: TasklistFilter) {
        this._filterSubject.next(filter);
    }

    mapToTasklistItem(task: KwsTask): TasklistItem {
        return new KwsTasklistItem(task);
    }

    trackById(_: any, item: TasklistItem) {
        return item.id;
    }

    toggleItem(item: KwsTasklistItem) {
        // const index = this.selectedItems.indexOf(item);

        item.checked = !item.checked;
    }

    private log(message: string) {
        this.logger.log(`TaskListComponent: ${message}`);
    }
}
