import {
    Directive, Renderer2, OnInit, OnDestroy, Input, ElementRef, forwardRef,
    EventEmitter,
    HostBinding,
    HostListener,
    Output,
    NgZone
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subject, fromEvent, Subscription } from 'rxjs';
import { coerceBooleanProperty, insertAfter } from './utils';

/*
    inspiration:
        MatDatepickerInput, MatAutocompleteTrigger
 */

/** @docs-private */
export const FILE_PICKER_VALUE_ACCESSOR: any = {
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => FilePickerDirective),
    multi: true
};


export class FilePickerEvent {
    value: FileList | null;

    constructor(/** Reference to the component that emitted the event. */
        public target: FilePickerDirective,
        /** Reference to the native input element associated with the file picker. */
        public targetElement: HTMLElement) {
        this.value = this.target.value;
    }
}


@Directive({
    selector: '[appFilePicker]',
    providers: [
        FILE_PICKER_VALUE_ACCESSOR
    ]
})
export class FilePickerDirective implements ControlValueAccessor, OnInit, OnDestroy {
    // static nextId = 0;
    // id = `example-tel-input-${FilePickerDirective.nextId++}`;
    // describedBy = '';
    // stateChanges = new Subject<void>();
    // focused = false;
    // errorState = false;
    // controlType = 'file-picker';

    private _fileInputEl: HTMLInputElement | undefined;
    private _fileInputElChangeSub: Subscription = Subscription.EMPTY;

    @Input()
    get value(): FileList | null { return this._value; }
    set value(value: FileList | null) {
        const oldFile = this.value;
        this._value = value;

        // FIXME:
        // this has to be here, because fileInput change event does not emit after control's reset
        // for some unknown reason
        this.destroyFileInputElement();
        this.createFileInputElement();
    }
    private _value: FileList | null = null;

    @Input()
    set multiple(v: boolean) {
        this._multiple = coerceBooleanProperty(v);

        if (this._fileInputEl) {
            this._renderer.setProperty(this._fileInputEl, 'multiple', this._multiple);
        }
    }
    private _multiple = false;

    @Input()
    set accept(v: string) {
        // TODO: validation
        this._accept = v;

        if (this._fileInputEl) {
            this._renderer.setProperty(this._fileInputEl, 'accept', this._accept);
        }
    }
    private _accept = '';

    @HostBinding()
    @Input()
    get disabled(): boolean { return this._disabled; }
    set disabled(value: boolean) {
        const newValue = coerceBooleanProperty(value);
        const element = this._hostRef.nativeElement;

        if (this._disabled !== newValue) {
            this._disabled = newValue;
            this._setDisabledClass(newValue);
        }

        // We need to null check the `blur` method, because it's undefined during SSR.
        if (newValue && element.blur) {
            // Normally, native input elements automatically blur if they turn disabled. This behavior
            // is problematic, because it would mean that it triggers another change detection cycle,
            // which then causes a changed after checked error if the input element was focused before.
            element.blur();
        }
    }
    private _disabled = false;

    private _onTouched = () => { };

    private _onChange: (value: any) => void = () => { };


    private _onFileInputChange = (ev: Event) => {
        this._isDialogOpen = false;
        const files = (ev.target as any).files;
        this._value = files;
        this._onChange(files);
        this._onTouched();
    }

    _isDialogOpen = false;

    constructor(
        private _ngZone: NgZone,
        private _hostRef: ElementRef<HTMLElement>,
        private _renderer: Renderer2
    ) { }

    ngOnInit() {
        this.createFileInputElement();
    }

    ngOnDestroy() {

    }

    createFileInputElement() {
        const inputEl = this._renderer.createElement('input') as HTMLInputElement;

        this._renderer.setProperty(inputEl, 'type', 'file');
        this._renderer.setStyle(inputEl, 'display', 'none');

        this._renderer.setProperty(inputEl, 'accept', this._accept);
        this._renderer.setProperty(inputEl, 'multiple', this._multiple);

        this._ngZone.runOutsideAngular(() => {
            this._fileInputElChangeSub = fromEvent(inputEl, 'change').subscribe(this._onFileInputChange);
        });

        this._fileInputEl = inputEl;

        insertAfter(this._fileInputEl, this._hostRef.nativeElement, this._renderer);
    }

    destroyFileInputElement() {
        if (this._fileInputEl) {
            if (this._renderer.destroyNode) {
                this._renderer.destroyNode(this._fileInputEl);
            }
        }

        this._fileInputElChangeSub.unsubscribe();
    }

    writeValue(value: any): void {
        this.value = value;
    }

    registerOnChange(fn: any): void {
        this._onChange = fn;
    }

    registerOnTouched(fn: any): void {
        this._onTouched = fn;
    }

    setDisabledState?(isDisabled: boolean): void {
        this.disabled = isDisabled;
    }

    /** Handles blur events on the input. */
    // @HostListener('blur')
    // private _onBlur() {
    //     // this._onTouched();
    //     // if (!this._value) { // becomes invalid is nothing selected
    //     //     this._onChange(null);
    //     // }
    // }

    @HostListener('click')
    onClick() {
        this._isDialogOpen = true;
        if (this._fileInputEl) { this._fileInputEl.click(); }
    }

    @HostListener('window:focus')
    onWindowFocus() {
        this._ngZone.runOutsideAngular(() => {
            setTimeout(() => { // make sure that file input change event does not run after this
                if (this._isDialogOpen) {
                    this._isDialogOpen = false;
                    if (!this._value) { // becomes invalid if nothing is selected
                        this._onChange(null);
                    }
                    this._onTouched();
                }
            }, 500);
        });
    }

    private _setDisabledClass(isDisabled: boolean) {
        if (isDisabled) {
            this._renderer.addClass(this._hostRef.nativeElement, 'disabled');
        } else {
            this._renderer.removeClass(this._hostRef.nativeElement, 'disabled');
        }
    }

}
