import * as Faker from 'faker';
import * as d3 from 'd3';

// Faker.locale = "cz";
// (window as any).Faker = Faker;

const AUDIO_FILE_EXTENSIONS = ['wav'];
const FORBIDDDEN_KEYWORD_CHARS_RE = /[\d\s]/;

export function getFakeKeywords(amount: number): string[] {
    const keywords: string[] = new Array(amount);

    const keywordIsValid = (keyword: string) => {
        return !FORBIDDDEN_KEYWORD_CHARS_RE.test(keyword);
    };

    let kw = '';
    for (let i = 0; i < amount; i++) {
        do {
            kw = Faker.random.word();
        } while (!keywordIsValid(kw));

        keywords[i] = kw;
    }

    return keywords;
}


export function getFakeAudioFilenames(amount: number): string[] {
    const filenames: string[] = new Array(amount);

    let fname = '';
    for (let i = 0; i < amount; i++) {
        fname = Faker.system.fileName();
        const fnameParts = fname.split('.');
        const audioFileExt = getRandomAudioFileExtension();
        if (fnameParts.length > 1) {
            fnameParts[fnameParts.length - 1] = audioFileExt;
        } else {
             fnameParts.push(audioFileExt);
        }
        filenames[i] = fnameParts.join('.');
    }

    return filenames;
}

export function getFakeAudioDurations(amount: number, expectedValue: number, standardDeviation: number): number[] {
    const durations = new Array(amount);
    const getRandom = d3.randomNormal(expectedValue, standardDeviation);

    for (let i = 0; i < amount; i++) {
        durations[i] = getRandom();
    }

    return durations;
}


export function getRandomAudioFileExtension(): string {
    const len = AUDIO_FILE_EXTENSIONS.length;
    return AUDIO_FILE_EXTENSIONS[Math.floor(Math.random() * len)];
}
