import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { scan, shareReplay, startWith, map } from 'rxjs/operators';
import { KwsTaskResults, KwsTask } from '../models';
import { uuid } from 'src/app/shared/utils';
import { TimeService } from '../time/time.service';
import { LoggerService } from '../logger/logger.service';

/*
    STORE
        submitted tasks
        results

*/


@Injectable({
    providedIn: 'root'
})
export class KwsTasksStoreService {

    // TODO: make tasks a hashmap for faster access using uuid
    // TODO: show observers  (subject.observers)
    private readonly _tasksSubject = new BehaviorSubject<KwsTask[]>([]);

    private readonly _taskCompletedSubject = new Subject<KwsTask>();
    private readonly _taskAddedSubject = new Subject<KwsTask>();
    private readonly _taskUpdatedSubject = new Subject<KwsTask>();
    private readonly _taskRemovedSubject = new Subject<KwsTask>();

    private readonly _taskSelectedSubject = new Subject<KwsTask>();

    tasks$ = this._tasksSubject.asObservable();
    taskCompleted$ = this._taskCompletedSubject.asObservable();
    taskAdded$ = this._taskAddedSubject.asObservable();
    taskUpdated$ = this._taskUpdatedSubject.asObservable();
    taskRemoved$ = this._taskRemovedSubject.asObservable();

    taskSelected$ = this._taskSelectedSubject.asObservable();


    readonly completedTasks$ = this.tasks$.pipe(
        map(tasks => tasks.filter(task => task.isCompleted))
    );

    constructor(
        private timeService: TimeService,
        private logger: LoggerService
    ) {
        (window as any).kwsTasksStore = this;
    }

    init(tasks: KwsTask[]) {
        this.log('initialization');
        this._tasksSubject.next(tasks);
    }

    // getTasks(): KwsTaskData[] {
    //     return this._tasksSubject.getValue();
    // }

    get tasks(): KwsTask[] {
        return this._tasksSubject.getValue();
    }

    private _setTasks(tasks: KwsTask[]) {
        this._tasksSubject.next(tasks);
    }

    addTask(task: KwsTask) {
        this.log(`adding task: ${task.name} (${task.id})`);
        const taskId = this.getNewId();
        this._setTasks([
            ...this.tasks,
            { ...task, id: taskId }
        ]);
        this._taskAddedSubject.next(task);
        return taskId;
    }

    updateTask(task: KwsTask) {
        this.log(`updating task (${task.id})`);
        const tasks = this.tasks;
        const index = tasks.findIndex(_task => _task.id === task.id);
        if (index !== -1) {
            this.tasks[index] = task;
            this._setTasks([...tasks]);
            return true;
        }
        return false;
    }

    getTask(taskId: string): KwsTask | undefined {
        this.log(`getting task by id (${taskId})`);
        return this.tasks.find(task => task.id === taskId);
    }


    getTaskResults(taskId: string): KwsTaskResults | undefined {
        const task = this.getTask(taskId);
        return task && task.results;
    }

    updateTaskResults(taskId: string, results: KwsTaskResults): boolean {
        this.log(`updating task (${taskId}) results`);
        const task = this.getTask(taskId);
        if (task) {
            this.updateTask({
                ...task,
                results,
                completionTime: this.timeService.currentTime
            });
            return true;
        }
        return false;
    }


    assignTaskResults(taskId: string, results: KwsTaskResults): boolean {
        // TODO: assign completion time using KwsClientUtils
        return this.updateTaskResults(taskId, results);
    }

    /*
    *   TODO: set results on completion
    */
    setCompleted(taskId: string, isCompleted: boolean, completionTime: number) {
        this.log(`setting task (${taskId}) completion`);
        const task = this.tasks.find(_task => _task.id === taskId);

        if (task) {
            // we need to make a new copy of tasks array, and the task as well
            const index = this.tasks.indexOf(task);
            this.tasks[index] = {
                ...task,
                isCompleted,
                completionTime
            };
            this._setTasks([...this.tasks]);
        }
    }

    removeTask(taskId: string): boolean {
        this.log(`removing task (${taskId})`);
        const tasks = this.tasks;
        const index = tasks.findIndex(_task => _task.id === taskId);
        if (index !== -1) {
            tasks.splice(index, 1);
            this._setTasks([...tasks]);
            return true;
        }
        return false;
    }

    selectTask(taskId: string): boolean {
        this.log(`selecting task (${taskId})`);
        const selectedTask = this.tasks.find(task => task.id === taskId);
        if (selectedTask) {
            this._taskSelectedSubject.next(selectedTask);
            return true;
        }
        return false;
    }

    taskExists(taskId: string): boolean {
        this.log(`checking if task (${taskId}) exists`);
        return this.tasks.some(task => task.id === taskId);
    }

    private getNewId(): string {
        return uuid();
    }

    private log(message: string) {
        this.logger.log(`KwsTasksStoreService: ${message}`);
    }
}





