import { Injectable, Inject, InjectionToken, Injector } from '@angular/core';
import { Observable, timer, of as observableOf, merge, iif, throwError, of } from 'rxjs';
import { map, take, mapTo, endWith, mergeMap, tap, filter } from 'rxjs/operators';
import { KwsApiServiceBase } from './kws-api.service.base';
import { KwsAudioDataService } from '../audio';
import { KwsClientConfigService } from '../kws-client/kws-client-config.service';
import { KwsClientUtilsService } from '../kws-client/kws-client-utils.service';
import { LoggerService } from '../logger/logger.service';
import { KwsTask, UploadEvent, UploadEventType, UploadProgressEvent, UploadResponse, KwsTaskResults } from '../models';
import { readVocabFile, Vocab } from 'src/app/shared/utils';
import { generateMockKeywordCandidates } from '../utils';

/*
 TODO:

     []  get from vocabs, languages, datasetNames from config
     []  message service (errors, logs)

     []  use Injection for passing the storage key

*/



export const KWS_MOCK_CANDIDATES_STORAGE_KEY_TOKEN = new InjectionToken<string>('kws-mock-candidates-storage-key');


@Injectable({
    providedIn: 'root',
})
export class MockKwsApiService extends KwsApiServiceBase {

    private mockCandidatesStorageKey = 'kws-mock-candidates';

    vocabFile: File | null = null;
    audioFiles: File[] | null = null;

    constructor(
        private audioDataService: KwsAudioDataService,
        // private injector: Injector,
        private config: KwsClientConfigService,
        private utils: KwsClientUtilsService,
        protected logger: LoggerService,
        // @Inject(KWS_MOCK_CANDIDATES_STORAGE_KEY_TOKEN) private mockCandidatesStorageKey: string,
    ) {
        super(logger);
    }


    postTask(task: KwsTask): Observable<UploadEvent<any>> {
        if (!task.audioFiles || !task.vocabFile) {
            const message = 'postTask failed: task does not have vocabFile or audioFiles';
            this.logError(message);
            return of({ type: UploadEventType.Failure, message } as any);
        }

        const vocabKey = this.utils.resolveVocabKey(task.vocabFile.name);

        this.vocabFile = task.vocabFile;
        this.audioFiles = task.audioFiles;

        return timer(100, 100).pipe(
            take(10),
            map((value) => {
                return {
                    type: UploadEventType.Progress,
                    percentDone: value / 10
                } as UploadProgressEvent;
            }),
            endWith({
                type: UploadEventType.Response,
                body: null
            } as UploadResponse<any>)
        );
    }


    getTaskResults(taskId: string): Observable<KwsTaskResults> {

        if (this.vocabFile) {
            if (this.utils.resolveVocabKey(this.vocabFile.name) !== 'vocfile') {
                const err = new Error('only vocab of type ".voc" is accepted in test mode');
                console.error(err.message);
                return throwError(err);
            }

            return observableOf(this.vocabFile).pipe(
                mergeMap(file => readVocabFile(file)),
                mergeMap((vocab) => {
                    return this.audioDataService.ready$.pipe(
                        tap((r) => {
                            console.log('audioPlayer.ready$: ', r);
                        }),
                        filter(Boolean),
                        take(1),
                        mapTo(vocab)
                    );
                }),
                map((vocab: Vocab) => {
                    const mockCandidates = [];
                    const vocabWords = [...vocab.words.keys()];

                    for (const [filename, audioBuffer] of this.audioDataService.entries) {
                        mockCandidates.push(
                            ...generateMockKeywordCandidates(vocabWords, filename, audioBuffer.duration)
                        );
                    }

                    for (let i = 0; i < mockCandidates.length; i++) {
                        mockCandidates[i].id = i;
                    }

                    // localStorage.setItem(this.mockCandidatesStorageKey, JSON.stringify(mockCandidates));

                    return {
                        candidates: mockCandidates
                    };
                })
            );
        } else {
            const candidatesJson = localStorage.getItem(this.mockCandidatesStorageKey);

            if (candidatesJson !== null) {
                return observableOf({
                    candidates: JSON.parse(candidatesJson)
                });
            }

            return observableOf({
                candidates: []
            });
        }
    }
}
