import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { TasklistFilter } from '../models/tasklist.model';

// CTA ... Call To Action

@Component({
    selector: 'app-tasklist-cta-bar',
    templateUrl: './tasklist-cta-bar.component.html',
    styleUrls: ['./tasklist-cta-bar.component.scss']
})
export class TasklistCtaBarComponent {

    @Input() filter: TasklistFilter | undefined;
    @Input() showActionButtons = true;
    @Output() filterChange = new EventEmitter<TasklistFilter>();
    @Output() checkAll = new EventEmitter<void>();
    @Output() uncheckAll = new EventEmitter<void>();

}
