import { NgModule } from '@angular/core';
import { BrowserModule, HAMMER_GESTURE_CONFIG } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { GestureConfig } from '@angular/material/core';
import { PreloadAllModules, RouterModule } from '@angular/router';

// import { MATERIAL } from './material';
import { APP_ROUTES } from './app.routes';
import { AppComponent } from './app.component';
import { TaskResultsModule } from './task-results/task-results.module';
import { TaskSubmissionModule } from './task-submission/task-submission.module';
import { CustomMaterialModule } from './custom-material.module';
import { HttpClientModule } from '@angular/common/http';
import { FilePickerModule } from './shared/file-picker/file-picker.module';
import { SharedModule } from './shared/shared.module';
import { TasklistModule } from './tasklist/tasklist.module';
import { KwsApiServiceBase } from './core/kws-api/kws-api.service.base';
import { MockKwsApiService } from './core/kws-api/mock-kws-api.service';

@NgModule({
    declarations: [
        AppComponent,
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        TasklistModule,
        TaskSubmissionModule,
        TaskResultsModule,
        RouterModule.forRoot(APP_ROUTES, {
            preloadingStrategy: PreloadAllModules,
            // enableTracing: true
        }),
        SharedModule,
        CustomMaterialModule,
        HttpClientModule
    ],
    providers: [
        { provide: KwsApiServiceBase, useClass: MockKwsApiService },
        { provide: HAMMER_GESTURE_CONFIG, useClass: GestureConfig },
    ],
    bootstrap: [AppComponent]
})
export class AppModule { }
