import { Component, OnInit, ChangeDetectionStrategy, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd } from '@angular/router';
import { Observable, merge } from 'rxjs';
import { filter, mapTo } from 'rxjs/operators';
import { uuid } from './shared/utils';
import * as Faker from 'faker';
import { LocalStorageService } from './core/local-storage/local-storage.service';
import browser from 'browser-detect';
import { environment } from 'src/environments/environment';
import { KwsClientService } from './core/kws-client/kws-client.service';

const APP_STATE_STORAGE_KEY = 'state';


@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class AppComponent implements OnInit {
    loading$?: Observable<boolean>;

    // items$: Observable<any>

    isProd = environment.production;

    navigation = [
        { link: 'about', label: 'anms.menu.about' },
        { link: 'feature-list', label: 'anms.menu.features' },
        { link: 'examples', label: 'anms.menu.examples' }
    ];

    constructor(
        private kwsClient: KwsClientService,
        private router: Router,
        private storageService: LocalStorageService,
    ) {

        (window as any).uuid = uuid;
        (window as any).Faker = Faker;

        (Faker.locale as any) = 'cz';

    }

    ngOnInit() {
        this.storageService.testLocalStorage();

        const appState = JSON.parse(this.storageService.getItem(APP_STATE_STORAGE_KEY));

        // TODO: check appState before passing
        this.kwsClient.init(appState);



        if (AppComponent.isIEorEdgeOrSafari()) {
            // disable animations
        }

        const navigationStart$ = this.router.events.pipe(
            filter(event => event instanceof NavigationStart),
            mapTo(true)
        );

        const navigationEnd$ = this.router.events.pipe(
            filter(event => event instanceof NavigationEnd),
            mapTo(false)
        );

        this.loading$ = merge(navigationStart$, navigationEnd$);
    }

    private static isIEorEdgeOrSafari() {
        return ['ie', 'edge', 'safari'].includes(String(browser().name));
    }

    @HostListener('window:beforeunload', ['$event'])
    beforeUnload($event: any) {
        // const appStateJson = JSON.stringify(this.kwsClient.getState());

        // this.storageService.setItem(APP_STATE_STORAGE_KEY, appStateJson);
    }
}
