import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TaskSubmissionComponent } from './task-submission.component';
import { ReactiveFormsModule } from '@angular/forms';
import { SharedModule } from '../shared/shared.module';
import { ConfirmationDialogComponent } from '../shared/confirmation-dialog/confirmation-dialog.component';
import { CustomMaterialModule } from '../custom-material.module';
import { RouterModule, Routes } from '@angular/router';
import { FilePickerModule } from '../shared/file-picker/file-picker.module';

const TASK_SUBMISSION_ROUTES: Routes = [
    {
        path: 'new-task',
        component: TaskSubmissionComponent
    }
];


@NgModule({
    declarations: [
        TaskSubmissionComponent,
        ConfirmationDialogComponent
    ],
    imports: [
        CommonModule,
        ReactiveFormsModule,
        SharedModule,
        CustomMaterialModule,
        RouterModule.forChild(TASK_SUBMISSION_ROUTES),
        FilePickerModule,
    ],
    entryComponents: [
        ConfirmationDialogComponent
    ]
})
export class TaskSubmissionModule { }
