import { Component, OnInit, ElementRef, ChangeDetectionStrategy, ViewEncapsulation, ChangeDetectorRef, EventEmitter, Input, ViewChild, SimpleChanges, NgZone, Output, OnChanges, AfterViewInit, OnDestroy } from '@angular/core';
import { Subject, fromEvent } from 'rxjs';
import { RectMargin } from '../models';
import { tap, debounceTime, startWith, takeUntil, filter, switchMap, pairwise } from 'rxjs/operators';
import { KwsAudioDataService } from 'src/app/core/audio';
import * as d3 from 'd3';
import { KwsTasksStoreService } from 'src/app/core/kws-tasks-store/kws-tasks-store.service';
import { LoggerService } from 'src/app/core/logger/logger.service';


const RIGHT_MOUSE_BTN = 2; // secondary button
const SPOT_BAR_BODY_NTH_CHILD = 1;
const SPOT_BAR_BORDER_NTH_CHILD = 2;

@Component({
    selector: 'app-kws-chart',
    templateUrl: './kws-chart.component.html',
    styleUrls: ['./kws-chart.component.scss'],
    encapsulation: ViewEncapsulation.None,
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class KwsChartComponent implements AfterViewInit, OnChanges, OnDestroy {

    get runChangeDetection() {
        console.log('spot-chart: runChangeDetection');
        return true;
    }

    // -------------------------------config-----------------------------------
    margin: RectMargin = { top: 0, right: 0, bottom: 50, left: 0 };

    idleDelay = 350;
    // zoomTransitionDuration = 750;
    viewUpdateDuration = 750;

    spotLabelFontSize = 15;
    spotLabelY = 50;
    spotBarY = 40;
    spotBarHeight = 15;
    spotBarBodyColor = 'lightgray'; // '#52d1cd';
    spotBarBorderColor = 'gray';
    spotBarBorderWidth = 2;

    spotLabelVisibilityThreshold = 500000;

    minBrushWidth = 50;
    numTicks = 7;

    minXDomainSize = 10000 * 10;

    maxZoomLevel = 4;
    domainShrink = 3;
    // domainExtensionPerZoomLevel = 1000;

    // ---------------------------------------------------------------------------


    @ViewChild('svg', { static: true }) svgRef!: ElementRef<SVGElement>;  // | undefined;


    @Input() taskId = '';

    @Input() items: KeywordCandidate[] = [];

    // @Input() audioSamples: number[] = [];
    // @Input() set domain(value: [number, number]) {
    //     this.xDomain = value;
    // }

    // @Input() audioSampleRate: number;

    @Output() clickedItem = new EventEmitter<KeywordCandidate>();

    private _destroy = new Subject();

    // ---------------------------------state-------------------------------------
    private firstDraw = true;
    private loading = true;
    private zoomLevel = 0;
    private spotLabelVisible = false;
    private idleTimeout: any;
    // ---------------------------------------------------------------------------


    private xScaleTime: d3.ScaleTime<number, number>;
    private xScale: d3.ScaleLinear<number, number>;
    private yScale: d3.ScaleLinear<number, number>;
    private xAxis!: d3.Axis<any>;  // TODO: remove the non-null assertion operator
    //    x2: d3.ScaleLinear<number, number>;
    //    y2: d3.ScaleLinear<number, number>;
    private xDomain: [number, number] = [0, 100000];
    private yDomain: [number, number] = [0, 100];

    private brush: d3.BrushBehavior<any>;

    get xDomainSize() {
        const xDomain = this.xScale.domain();
        return Math.abs(xDomain[1] - xDomain[0]);
    }

    getKeywordCandidateLabel = (kwc: KeywordCandidate) => {
        return this.spotLabelVisible ? kwc.keyword : '';
    }


    constructor(
        private kwsTaskStore: KwsTasksStoreService,
        private kwsAudioData: KwsAudioDataService,
        private hostRef: ElementRef<HTMLElement>,
        private ngZone: NgZone,
        private changeDetectorRef: ChangeDetectorRef,
        private logger: LoggerService) {

        // DEBUG
        const w = window as any;
        w.chart = this;

        this.changeDetectorRef.detach();

        this.xScaleTime = d3.scaleTime();
        this.xScale = d3.scaleLinear();
        this.yScale = d3.scaleLinear();

        this.brush = d3.brushX()
            .on('end', () => this.brushEnded());
    }

    // ngAfterViewChecked() {
    //     console.log('spot-chart view checked')
    // }

    ngOnChanges(changes: SimpleChanges): void {

        if (changes.taskId) {
            const results = this.kwsTaskStore.getTaskResults(this.taskId);

            if (results) {
                this.items = results.candidates;
            }
        }

        if (!this.xScale) { return; }

        if (this.firstDraw) {
            this.build();
            this.firstDraw = false;
        }

        this.updateData();
    }

    ngAfterViewInit() {
        this.init();
    }

    private init() {
        if (this.svgRef === undefined) {
            throw new Error('svgRef is undefined');
        }

        const svg: SVGElement = this.svgRef.nativeElement;


        // this.xAxis = d3.axisBottom(this.xScale);

        fromEvent(window, 'resize').pipe(
            tap(() => this.loading = true),
            debounceTime(100),
            startWith(null),
            takeUntil(this._destroy)
        ).subscribe((event: Event | null) => {
            console.log('resize event', event);
            // this.ngZone.runOutsideAngular(() => {
            this.onResize();
            // });
            setTimeout(() => {
                this.loading = false;
                // this.changeDetectionRef.markForCheck();
            }, 0);
        });

        // this.ngZone.runOutsideAngular(() => {
        this.setupInput();
        // });

        // zoom
        // d3.select(this.svgRef.nativeElement).call(this.zoom);
    }

    private setupInput() {
        // if (this.svgRef === undefined) {
        //     throw new Error('svgRef is undefined');
        // }

        // new EventListenerOptions()
        this.svgRef.nativeElement.addEventListener('contextmenu', event => {
            event.preventDefault();
            return false;
        });

        const click$ = fromEvent<MouseEvent>(this.svgRef.nativeElement, 'click');
        const dblclick$ = fromEvent<MouseEvent>(this.svgRef.nativeElement, 'dblclick');

        const mouseDown$ = fromEvent<MouseEvent>(this.svgRef.nativeElement, 'mousedown');
        const mouseMove$ = fromEvent<MouseEvent>(this.svgRef.nativeElement, 'mousemove');
        const mouseUp$ = fromEvent<MouseEvent>(this.svgRef.nativeElement, 'mouseup');
        const mouseLeave$ = fromEvent<MouseEvent>(this.svgRef.nativeElement, 'mouseleave');



        // pan
        mouseDown$.pipe(
            tap((e: MouseEvent) => {
                console.log(`button: ${e.button}`, e);
            }),
            filter((e: MouseEvent) => e.button === RIGHT_MOUSE_BTN),
            switchMap(e => {
                return mouseMove$.pipe(
                    takeUntil(mouseUp$),
                    takeUntil(mouseLeave$),
                    pairwise()
                );
            }),
            takeUntil(this._destroy)
        ).subscribe((res: [MouseEvent, MouseEvent]) => {
            const shiftX = - (this.xScale.invert(res[1].clientX - res[0].clientX) - this.xScale.invert(0));
            this.shiftDomain(shiftX);
        });

        click$.pipe(takeUntil(this._destroy))
            .subscribe((event) => {
                if (event.target instanceof SVGElement) {
                    const target = event.target as SVGElement;

                    if (target.classList.contains('overlay')) {
                        // this.zoomIn(event.pageX);

                    } else if ('__data__' in target) {
                        const data = (event.target as any).__data__;

                        if (typeof data.timeFrom === 'number') {
                            this.clickedItem.emit(data);
                        }
                    }
                }
            });

        dblclick$.pipe(takeUntil(this._destroy))
            .subscribe((event) => {
                if (event.target instanceof SVGElement) {
                    const target = event.target as SVGElement;

                    if (target.classList.contains('overlay')) {
                        // this.resetZoom();
                    }
                }
            });

        // defer(() => of(doSomethingSync()));

        // this.dragSub = fromEvent(this.svgRef.nativeElement, 'dragstart').pipe(

    }


    private onResize() {

        // this.hostWidth = this.svgRef.nativeElement.clientWidth;
        // this.hostHeight = this.svgRef.nativeElement.clientHeight;
        if (!this.items) {
            return;
        }

        this.build();
        this.updateData();
    }

    private build() {
        // if (this.svgRef === undefined) {
        //     throw new Error('svgRef is undefined');
        // }

        const svg = d3.select(this.svgRef.nativeElement);
        const svgEl = svg.node();

        if (svgEl === null || svgEl.parentElement === null) {
            return;
        }

        const margin = this.margin;
        const x = this.xScale;
        const y = this.yScale;
        // const xAxis = this.xAxis;

        // x ... 100us - unit of time
        console.log('drawing spot chart', this.items);

        svg.attr('width', 0).attr('height', 0);

        const { width: fullWidth, height: fullHeight } = svgEl.parentElement.getBoundingClientRect();

        svgEl.innerHTML = '';

        svg.attr('width', fullWidth)
            .attr('height', fullHeight);

        const width = fullWidth - margin.left - margin.right;
        const height = fullHeight - margin.top - margin.bottom;

        svg.append('defs').append('svg:clipPath')
            .attr('id', 'clip')
            .append('svg:rect')
            .attr('width', width).attr('height', height)
            .attr('x', 0).attr('y', 0);

        this.brush.extent([
            [margin.left, margin.top],
            [fullWidth - margin.right, fullHeight - margin.bottom]
        ]);

        // this.ngZone.runOutsideAngular(() => {
        svg.append('g')
            .attr('class', 'brush')
            .call(this.brush);
        // });

        const g: d3.Selection<SVGGElement, unknown, null, undefined> = svg.append('g')
            .attr('class', 'focus')
            .attr('transform', `translate(${margin.left},${margin.top})`)
            .style('user-select', 'none');

        x.domain(this.xDomain)
            .range([0, width]);

        y.domain(this.yDomain)
            .range([height, 0]);

        this.xScaleTime.range(x.range());
        this.updateTimeScale();

        // TODO: move to ctor  (try to set scale)
        // this.xAxis.scale(,)
        this.xAxis = d3.axisBottom(this.xScaleTime)
            .tickFormat(this.xAxisFormat() as any);


        svg.append('g')
            .attr('class', 'axis axis--x')
            .attr('transform', `translate(0,${height})`)
            .call(this.xAxis);

        g.append('g').attr('class', 'spot-labels');
        g.append('g').attr('class', 'spot-bars');
    }

    private brushEnded() {
        // if (this.svgRef === undefined) {
        //     throw new Error('svgRef is undefined');
        // }

        console.log('brushended');

        const svg = d3.select(this.svgRef.nativeElement);

        const s = d3.event.selection;

        if (!s) {
            if (!this.idleTimeout) { return this.idleTimeout = setTimeout(() => this.clearIdleTimeout(), this.idleDelay); }
            this.xScale.domain(this.xDomain);
        } else {
            if (Math.abs(s[1] - s[0]) >= this.minBrushWidth) {
                this.xScale.domain([s[0], s[1]].map(this.xScale.invert, this.xScale));
            }
            svg.select('.brush').call(this.brush.move as any, null);
        }

        this.updateView(this.viewUpdateDuration);
    }

    /**
     * maybe updateElements is more appropriate
     *
     *  */
    private updateData() {
        const x = this.xScale;
        const oldX = x.copy();

        const svg = d3.select(this.svgRef.nativeElement);

        const barYScaled = this.yScale(this.spotBarY);
        const barHeightScaled = Math.abs(this.yScale(this.spotBarHeight) - this.yScale(0));
        const labelYScaled = this.yScale(this.spotLabelY);

        const t = d3.transition().duration(750);

        x.domain(this.xDomain);
        this.updateTimeScale();
        this.xAxis.ticks(Math.min(this.numTicks, this.items.length));
        svg.select('.axis--x').transition(t as any).call(this.xAxis as any);

        const reverseSpots = [...this.items].reverse();

        let bars = svg.select('.spot-bars') as any;

        let labels = svg.select('.spot-labels')
            .style('opacity', 0.0) as any;

        bars = bars.selectAll('.spot-bar')
            .data(reverseSpots, ((d: any) => d) as any)
            .join(
                (enter: any) => enter.append('g')
                    .attr('class', 'spot-bar')
                    .attr('transform', (d: any) => `translate(${oldX(d.timeFrom)},${barYScaled})`)
                    .call((g: any) => g.append('rect')
                        .attr('width', (d: any) => x(d.timeTo) - x(d.timeFrom))
                        .attr('height', barHeightScaled)
                        .attr('fill', this.spotBarBodyColor))
                    .call((g: any) => g.append('rect')
                        .attr('width', this.spotBarBorderWidth)
                        .attr('height', barHeightScaled)
                        .attr('fill', this.spotBarBorderColor)),

                // .attr('transform-origin', '50% 0')
                // .attr('x', d => oldX(d.timeFrom))
                // .attr('width', d => x(d.timeTo) - x(d.timeFrom))
                // .attr('y', barYScaled)
                (update: any) => update,
                (exit: any) => exit
                    .classed('exit', true)
                    .call((s: any) => s.transition(t)
                        .attr('transform', (d: any) => `translate(${x(d.timeFrom)},${barYScaled})`)
                        .call((g: any) => g.select(`:nth-child(${SPOT_BAR_BODY_NTH_CHILD})`)
                            .attr('fill', 'red'))
                        .remove())

            );


        labels = labels.selectAll('.spot-label')
            .data(reverseSpots, ((d: any) => d) as any)
            .join(
                (enter: any) => enter.append('text')
                    .attr('class', 'spot-label')
                    .text((d: KeywordCandidate) => d.keyword)
                    .attr('transform', (d: any) => `translate(${oldX(d.timeTo)},${labelYScaled})`)
                    .attr('font-size', this.spotLabelFontSize),
                // .attr('x', 8)
                (update: any) => update
                    .text((d: KeywordCandidate) => d.keyword),
                (exit: any) => exit
                    .classed('exit', true)
                    .call((s: any) => s.transition(t)
                        .attr('x', (d: any) => x(d.timeFrom))
                        .remove())
            );

        // .filter(':not(.exiting)')

        // const spotLabels = svg.select('.spot-labels').selectAll('text').data(this.spots) as any;

        const spotLabelYScaled = this.yScale(this.spotLabelY);

        // .filter(':not(.exit)')
        this.updateView(this.viewUpdateDuration, bars, labels);

        // .transition(t)
        // .attr('transform', d => `translate(${x(d.timeTo)},${spotLabelYScaled})`);
    }


    private updateView(transitionDuration = 0, spotBars: any = null, spotLabels: any = null) {
        const svg = d3.select(this.svgRef.nativeElement);
        const focus = svg.select('.focus');
        const x = this.xScale;

        const xDomain = x.domain();
        const xDomainSize = this.xDomainSize; // Math.abs(xDomain[1] - xDomain[0]);

        if (xDomainSize < this.minXDomainSize) {
            const domainPad = (this.minXDomainSize - xDomainSize) * 0.5;
            x.domain([xDomain[0] - domainPad, xDomain[1] + domainPad]);
        }

        this.updateTimeScale();

        // const barYScaled = this.yScale(this.spotBarY);
        // const spotBarHeightScaled = Math.abs(this.yScale(this.spotBarHeight) - this.yScale(0));
        const spotLabelYScaled = this.yScale(this.spotLabelY);
        const barYScaled = this.yScale(this.spotBarY);

        // move axis
        const t = d3.transition().duration(transitionDuration) as any;
        const xAxisSel = svg.select('.axis--x');

        xAxisSel.transition(t).call(this.xAxis as any);

        // move data
        spotBars = spotBars || focus.select('.spot-bars').selectAll('.spot-bar');
        spotLabels = spotLabels || focus.select('.spot-labels').selectAll('.spot-label');

        spotBars
            .transition(t)
            .attr('transform', (d: any) => `translate(${x(d.timeFrom)},${barYScaled})`)
            .call((g: any) => g.select(`:nth-child(${SPOT_BAR_BODY_NTH_CHILD})`)
                .attr('width', (d: any) => x(d.timeTo) - x(d.timeFrom)));

        // .call(g => g.select(':nth-child(2)').attr('width', d => x(d.timeTo) - x(d.timeFrom)));

        // .attr('x', (d) => x(d.timeFrom))
        // .attr('width', (d) => x(d.timeTo) - x(d.timeFrom));

        spotLabels
            .transition(t)
            .attr('transform', (d: any) => `translate(${x(d.timeFrom)},${spotLabelYScaled})`);

        // labels visibility
        this.spotLabelVisible = xDomainSize <= this.spotLabelVisibilityThreshold ? true : false;

        console.log('spotLableVisible:', this.spotLabelVisible);
        console.log('domainX: ', x.domain());

        focus.select('.spot-labels').transition(t)
            .style('opacity', () => {
                return this.spotLabelVisible ? 1.0 : 0.0;
            });

        // .attr('x', (d) => x(d.timeFrom))
        // .call(s => s.select('text')
        //         .text(this.getSpotLabel))
        // .call(s => s.select('rect')
        //         .transition(t)
        //         .attr('width', (d) => x(d.timeTo) - x(d.timeFrom)))
        // .transition(t)
        // .attr('trasform', d => `translate(${x(d.timeFrom)},${barYScaled})`);


        // TODO: smaller secondary ticks
        // xAxisSel.selectAll('.tick').select('text').nodes().forEach((el: SVGTextElement) => {
        //     el.style.fontWeight = 'bold';
        // })

        xAxisSel.selectAll('.tick').select('text')
            .filter((_, i, g: any) => g[i].innerHTML.includes('m'))
            .select((_, i, g: any) => g[i].parentNode)
            .call(g => g.select('line').attr('y1', -50))
            .call(g => g.select('text').style('font-weight', 'bold'));
    }

    private clearIdleTimeout() {
        this.idleTimeout = null;
    }

    private shiftDomain(sx: any) {
        this.xScale.domain(this.xScale.domain().map(x => x + sx));

        this.updateView(0);
    }

    private updateTimeScale() {
        this.xScaleTime.domain(this.xScale.domain().map(x => new Date(0.1 * x)));
    }

    private xAxisFormat() {
        const formatMillisecond = () => ''; // d3.timeFormat('%Lms');
        const formatSecond = d3.timeFormat('%-Ss');
        const formatMinute = d3.timeFormat('%-Hh %-Mm');
        const formatHour = d3.timeFormat('%-Hh');

        // Define filter conditions
        return function multiFormat(date: Date) {
            return (d3.timeSecond(date) < date ? formatMillisecond
                : d3.timeMinute(date) < date ? formatSecond
                    : d3.timeHour(date) < date ? formatMinute
                        : formatHour)(date);
        };
    }

    zoomIn(x: number) {
        this.zoomLevel++;
        if (this.zoomLevel > this.maxZoomLevel) {
            this.resetZoom();
            return;
        }

        console.log('zoom', this.zoomLevel);
        this.zoom(x, 1);
    }

    zoom(x: number, deltaScale: -1 | 1) {
        // if (level < 0) {
        //     throw new RangeError('level is out of valid range');
        // }
        const { left } = this.svgRef.nativeElement.getBoundingClientRect();
        const originX = this.xScale.invert(x - left);
        const halfNewXDomainSize = 0.5 * this.xDomainSize / this.domainShrink;

        this.xScale.domain([
            originX - halfNewXDomainSize,
            originX + halfNewXDomainSize
        ]);

        console.log('originX', originX);

        // this.xDomain[0] + this.domainExtensionPerZoomLevel
        this.updateView(this.viewUpdateDuration);
    }

    resetZoom() {
        this.zoomLevel = 0;

        this.xScale.domain(this.xDomain);

        this.updateView(this.viewUpdateDuration);
    }

    ngOnDestroy() {
        this._destroy.next();
        this._destroy.complete();
    }

    private isOverlay(elem: Element) {
        if (elem instanceof SVGElement) {
            return elem.classList.contains('overlay');
        }

        return false;
    }


    getContainerDims(): any {
        let width;
        let height;
        const hostElem = this.hostRef.nativeElement;

        if (hostElem.parentNode !== null) {
            // Get the container dimensions
            const dims = (hostElem.parentNode as any).getBoundingClientRect();
            width = dims.width;
            height = dims.height;
        }

        if (width && height) {
            return { width, height };
        }

        return null;
    }


    private log(message: string) {
        this.logger.log(message);
    }

    private logError(message: string) {
        this.logger.logError(message);
    }

}
