import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { KwsTasksStoreService } from '../core/kws-tasks-store/kws-tasks-store.service';

@Injectable({
    providedIn: 'root'
})
export class TaskExistsGuard implements CanActivate {

    constructor(
        private tasksStore: KwsTasksStoreService,
        private router: Router
    ) { }


    canActivate(snapshot: ActivatedRouteSnapshot) {
        // const taskId = parseInt(snapshot.params.task, 10);
        const taskId = snapshot.params.id;

        if (this.tasksStore.taskExists(taskId)) {
            this.tasksStore.selectTask(taskId);
            return true;
        }

        this.router.navigate(['/tasks']);
        return false;
    }
}
