export interface MockAudioFile {
    name: string;
    duration: number;
}


export function generateMockKwsTaskResults(vocabWords: string[], audioFiles: MockAudioFile[]): KwsTaskResults {
    const candidates: KeywordCandidate[] = [];

    audioFiles.forEach(file => {
        candidates.push(...generateMockKeywordCandidates(vocabWords, file.name, file.duration));
    });

    // assign an id to each candidate
    for (let i = 0; i < candidates.length; i++) {
        candidates[i].id = i;
    }

    return {
        candidates
    };
}


