export class Vocab {

    _words: Map<string, string[]>;

    constructor(data?: Map<string, string[]> | [string, string[]][]) {
        if (data instanceof Map) {
            this._words = data;
        } else if (Array.isArray(data)) {
            this._words = new Map<string, string[]>(data);
        } else {
            this._words = new Map();
        }
    }

    get size(): number { return this._words.size; }

    get words(): Map<string, string[]> { return this._words; }


    toJSON(): string {
        return JSON.stringify([...this.words.entries()]);
    }

    static fromEntries(entries: [string, string[]][]): Vocab {
        return new Vocab(new Map(entries));
    }

    // tslint:disable-next-line: member-ordering
    static fromVOC(str: string) {
        const map = new Map();
        const lines = str.match(/[^\r\n]+/g) as Array<any>;
        lines.shift();
        let word: string;
        for (let i = 0, len = lines.length; i < len; i++) {
            lines[i] = lines[i].match(/[^\s]+/g);
            lines[i].shift();
            word = lines[i].shift();
            map.set(word, lines[i]);
        }

        return new Vocab(map);
    }

    static fromLEX(str: string): Vocab {
        return new Vocab();
    }

    static fromJSON(str: string): Vocab {
        return Vocab.fromEntries(JSON.parse(str));
    }
}
