
export function parseTimeMs(timeStr: string): number {
    const parts = timeStr.split(':');
    const hours = parseInt(parts[0], 10);
    const minutes = parseInt(parts[1], 10);
    const seconds = parseFloat(parts[2]);
    return Math.trunc(1000 * (3600 * hours + 60 * minutes + seconds));
}


export function time100usToString(t: number): string {
    const h = Math.floor(t / 36000000);
    t -= (h * 36000000);
    const m = Math.floor(t / 600000);
    t -= (m * 600000);
    const s = Math.floor(t / 10000);
    t -= (s * 10000);

    return `${
        h.toString().padStart(2, '0')}:${
        m.toString().padStart(2, '0')}:${
        s.toString().padStart(2, '0')}.${
        t.toString().padStart(4, '0')}`;
}