import { readFileAsArrayBuffer } from '../../../shared/utils/file';

export function readAudioFile(ctx: AudioContext, file: File): Promise<AudioBuffer> {
    const reader = new FileReader();
    return new Promise((resolve, reject) => {
        reader.onload = () => {
            ctx.decodeAudioData(reader.result as ArrayBuffer, (audioBuffer) => {
                resolve(audioBuffer);
            }, (err) => {
                reject(err);
            });
        };
        reader.onerror = () => {
            reject(reader.error);
        };
        reader.readAsArrayBuffer(file);
    });

    // const buffer = await readFileAsArrayBuffer(file);
    // const audioBuffer = await ctx.decodeAudioData(buffer);

    // return audioBuffer;
}
