import { Component, OnInit, Input, Output, SimpleChanges, EventEmitter, OnChanges } from '@angular/core';
import { FormControl } from '@angular/forms';

@Component({
    selector: 'app-dropdown',
    templateUrl: './dropdown.component.html',
    styleUrls: ['./dropdown.component.scss']
})
export class DropdownComponent implements OnChanges {

    @Input() items: Array<any> | undefined;
    @Input() bindLabel: string | undefined;
    @Input() bindValue: string | undefined;
    @Input() selected: string | undefined;

    @Output() selectionChange = new EventEmitter<any>();

    select = new FormControl();

    ngOnChanges(changes: SimpleChanges) {
        if (changes.selected) {
            this.select.setValue(changes.selected.currentValue);
        }
    }
}
