

// TODO: consider adding genericity

export enum UploadEventType {
    Progress, Response, Failure, Unknown
}

export type UploadEvent<T> = UploadResponse<T> | UploadProgressEvent | UploadFailureEvent | UploadUnknownEvent;

export interface UploadResponse<T> {
    type: UploadEventType.Response;
    body: T;
}

export interface UploadProgressEvent {
    type: UploadEventType.Progress;
    percentDone: number;
}

export interface UploadFailureEvent {
    type: UploadEventType.Failure;
    message: string;
}

export interface UploadUnknownEvent {
    type: UploadEventType.Unknown;
}
